@import '../../Assets/SCSS/ColourPalette.scss';

.SearchDetails {
    margin-top: 50px;

    h5 {
        font-size: 40px;
        margin-bottom: 50px;
    }

    .search-item {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 1px solid #efefef;
        display: inline-block;
        width: 100%;
        color: #000;
        
        img {
            width: 230px;
            border-radius: 5px;
            float: left;
            margin-right: 20px;
        }
        p {
            overflow: hidden;
            height: 70px;
        }
    }
}

.emptySearch {
    padding: 20px;
    padding-left: 0px;
    font-size: 20px;
    background: #24a7df;
    border-radius: 5px;
    text-align: center;
    margin-bottom: 20px;
    color: #fff;
}